import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { WorkingService } from '../../service/working.service'
import { Router } from '@angular/router';


@Component({
  selector: 'app-work-main',
  templateUrl: './work-main.component.html',
  styleUrls: ['./work-main.component.scss']
})
export class WorkMainComponent implements OnInit,OnDestroy{

works:string[];
workingSub :Subscription;

  constructor(private workingService : WorkingService,
    private router:Router) { }

  ngOnInit(): void {
  	this.subToWork();
  }

  subToWork(){
  	this.workingSub = this.workingService.workingSubject.subscribe((works)=>{
  		this.works = works;
  		this.works.reverse();
      console.log('set');
  	});
  	this.workingService.emitWorking();
  }

  ngOnDestroy(){
  	this.workingSub.unsubscribe();
  }



}
