import { Injectable } from '@angular/core';
import * as firebase from 'firebase';
import { Subject } from 'rxjs/Subject';

@Injectable({
  providedIn: 'root'
})
export class StudiesService {

studies:string[];
studiesSubject = new Subject<string[]>();

  constructor() { 
  	  	 	firebase.database().ref('/public/studies').on('value',(data)=>{
  				this.studies = data.val() ? data.val() : [];
  				this.emitStudies();
  			});
  }

  emitStudies(){
  	if(this.studies) this.studiesSubject.next(this.studies.slice());
  }

  saveStudie(studie,index){
      if(index < this.studies.length ) 
        firebase.database().ref("/public/studies/"+index).set(studie);
      else{
        firebase.database().ref("/public/studies/"+this.studies.length).set(studie);
      }
    }

    eraseStudie(index:number){
      if(index < this.studies.length ){
        this.studies.splice(index,1);
        console.log(this.studies);
        firebase.database().ref("/public/studies/").set(this.studies);
      }
    }


}
