import { Injectable } from '@angular/core';
import * as firebase from 'firebase';
import { Subject } from 'rxjs/Subject';

@Injectable({
  providedIn: 'root'
})
export class SkillsService {

 skills:Map<string,Object>;
skillsSubject = new Subject<Map<string,Object>>();

  constructor() { 
  	  	 	firebase.database().ref('/public/skills').on('value',(data)=>{
  				this.skills = data.val() ? data.val() : [];
  				console.log("skills:" + this.skills['Maintainer']['Docker'])
  				this.emitSkills();
  			});
  }

  emitSkills(){
  	if(this.skills) this.skillsSubject.next(this.skills);
  }
}
