import { Injectable } from '@angular/core';
import * as firebase from 'firebase';
import { Subject } from 'rxjs/Subject';

@Injectable({
  providedIn: 'root'
})
export class HobbiesService {


	hobbies:string[];
	hobbiesSubject = new Subject<string[]>();

  constructor() { 
  	firebase.database().ref('/public/hobbies').on('value',(data)=>{
  				this.hobbies = data.val() ? data.val() : [];
  				this.emitHobbies();
  			});
  }

  emitHobbies(){
  	if(this.hobbies) this.hobbiesSubject.next(this.hobbies.slice());
  }

    saveHobby(work,index){
      if(index < this.hobbies.length ) 
        firebase.database().ref("/public/hobbies/"+index).set(work);
      else{
        firebase.database().ref("/public/hobbies/"+this.hobbies.length).set(work);
      }
    }

    eraseHobby(index:number){
      if(index < this.hobbies.length ){
        this.hobbies.splice(index,1);
        console.log(this.hobbies);
        firebase.database().ref("/public/hobbies/").set(this.hobbies);
      }
    }
}
