import { Injectable } from '@angular/core';
import * as firebase from 'firebase';
import { Subject } from 'rxjs/Subject';

@Injectable({
  providedIn: 'root'
})
export class WorkingService {

	works:string[];
	workingSubject = new Subject<string[]>();

  constructor() { 
  	firebase.database().ref('/public/working').on('value',(data)=>{
  				this.works = data.val() ? data.val() : [];
  				this.emitWorking();
  			});
  }

  emitWorking(){
  	if(this.works) this.workingSubject.next(this.works.slice());
  }

    saveWork(work,index){
      if(index < this.works.length ) 
        firebase.database().ref("/public/working/"+index).set(work);
      else{
        firebase.database().ref("/public/working/"+this.works.length).set(work);
      }
    }

    eraseWork(index:number){
      if(index < this.works.length ){
        this.works.splice(index,1);
        console.log(this.works);
        firebase.database().ref("/public/working/").set(this.works);
      }
    }
}
