import { Component, OnInit, OnDestroy } from '@angular/core';
import { UserService } from '../vitrine/service/user.service'
import { User } from '../vitrine/models/user.model';
import { Subscription } from 'rxjs/Subscription';
import { AuthGardService } from '../vitrine/service/auth-gard.service';
import { AuthService } from '../vitrine/service/auth.service';

@Component({
  selector: 'app-navbar-header',
  templateUrl: './navbar-header.component.html',
  styleUrls: ['./navbar-header.component.scss']
})
export class NavbarHeaderComponent implements OnInit, OnDestroy {

user = new User('','','');
userSubscription :Subscription;

isAuth = false;
isAuthSub:Subscription;

  constructor(private userService : UserService,
    private authGardService : AuthGardService,
    private authService :AuthService) { }

  ngOnInit(): void {
	this.subscribeToUser();
  this.subscribeToAuth();

  }

  subscribeToUser(){
  	   this.userSubscription = this.userService.userSubject.subscribe((userdata)=>{
   	this.user = userdata;
   });
   this.userService.emitUser();
  }

  subscribeToAuth(){
    this.isAuthSub = this.authGardService.isAuthSubject.subscribe((isAuth)=>{

      this.isAuth = isAuth;
    });
    this.authGardService.emitIsAuth();
  }

  onSignOut(){
    this.authService.signOut();
  }

  ngOnDestroy(){
    this.isAuthSub.unsubscribe();
    this.userSubscription.unsubscribe();
  }

}
