import { Injectable } from '@angular/core';
import * as firebase from 'firebase';
import { Subject } from 'rxjs/Subject';

@Injectable({
  providedIn: 'root'
})
export class ProjectsService {

  projects:string[];
projectSubject = new Subject<string[]>();

  constructor() { 
  	  	 	firebase.database().ref('/public/project').on('value',(data)=>{
  				this.projects = data.val() ? data.val() : [];
  				this.emitProjects();
  			});
  }

  emitProjects(){
  	if(this.projects) this.projectSubject.next(this.projects.slice());
  }

  saveProject(project,index){
      if(index < this.projects.length ) 
        firebase.database().ref("/public/project/"+index).set(project);
      else{
        firebase.database().ref("/public/project/"+this.projects.length).set(project);
      }

      firebase.database().ref('/public/projects-info/project-' + project.id).once("value", snapshot => {
   			if (snapshot.exists()){
      			console.log("exists!");
   			}else{
   				const newInfo ={name:project.name};
   				firebase.database().ref('/public/projects-info/project-' + project.id).set(newInfo);
   			}
		});
    }

    eraseProject(index:number){
      if(index < this.projects.length ){
      	const id = this.projects[index]['id'];
        this.projects.splice(index,1);
        firebase.database().ref("/public/project/").set(this.projects);
        firebase.database().ref('/public/projects-info/project-' + id).remove();
      }
    }

   getSingleProject(id: number) {
    return new Promise(
      (resolve, reject) => {
        firebase.database().ref('/public/projects-info/project-' + id).once('value').then(
          (data) => {
            resolve(data.val());
          }, (error) => {
            reject(error);
          }
        );
      }
    );
  }

  getDownloadImage(image){
  	return new Promise(
  		(resolve, reject) => {
   		firebase.storage().ref().child("/public/project/"+image).getDownloadURL().then((url:string)=>{
   				resolve(url);

  			},(error)=>{
  				reject(error);
  			});
   	});
   }

   saveSingleProject(projectId,project){
   	firebase.database().ref('/public/projects-info/project-' + projectId).set(project);
   }


    uploadFile(file : File){
  	return new Promise((resolve,reject)=>{
  		const almostUniqueFileName = Date.now().toString();
  		const upload = firebase.storage().ref()
  		.child('/public/project/'+ almostUniqueFileName + file.name)
  		.put(file);

  		upload.on(firebase.storage.TaskEvent.STATE_CHANGED, ()=>{
  			console.log("chargment ... ");
  		},(error) =>{
  			console.log("erreur : " + error);
  			reject()
  		},()=>{
        	resolve(almostUniqueFileName + file.name);
  		});
  	});
  }

 	deletePhoto(name:string){
 		firebase.storage().ref().child('/public/project/'+name).delete().then(()=>{
        console.log( name +' deleted')
      },(error)=>{
        console.log(error);
      });
 	}
}
