import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { HttpClientModule} from '@angular/common/http';
import {Routes,RouterModule,CanActivate} from '@angular/router';



import { NavigateService } from './vitrine/service/navigate.service';
import { UserService } from './portfolio/service/user.service';
import { AuthService } from './portfolio/service/auth.service';
import { AuthGardService } from './portfolio/service/auth-gard.service';

import { VitrineComponent } from './vitrine/vitrine.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { NavbarHeaderComponent } from './navbar-header/navbar-header.component';
import { MainComponent } from './vitrine/main/main.component';
import { ProjectComponent } from './vitrine/project/project.component';
import { ProjectMainComponent } from './vitrine/project/project-main/project-main.component';
import { LearningComponent } from './vitrine/learning/learning.component';
import { LearningMainComponent } from './vitrine/learning/learning-main/learning-main.component';
import { HobbiesComponent } from './vitrine/hobbies/hobbies.component';
import { HobbiesMainComponent } from './vitrine/hobbies/hobbies-main/hobbies-main.component';
import { WorkComponent } from './vitrine/work/work.component';
import { WorkMainComponent } from './vitrine/work/work-main/work-main.component';
import { AuthComponent } from './vitrine/auth/auth.component';
import { AdminUserComponent } from './vitrine/admin/admin-user/admin-user.component';
import { AdminWorksComponent } from './vitrine/admin/admin-works/admin-works.component';
import { AdminStudiesComponent } from './vitrine/admin/admin-studies/admin-studies.component';
import { AdminHobbiesComponent } from './vitrine/admin/admin-hobbies/admin-hobbies.component';
import { PortfolioComponent } from './portfolio/portfolio.component';
import { HeaderComponent } from './portfolio/header/header.component';
import { StudiesfolioComponent } from './portfolio/studiesfolio/studiesfolio.component';
import { FooterComponent } from './portfolio/footer/footer.component';
import { WorkfolioComponent } from './portfolio/workfolio/workfolio.component';
import { ProjectfolioComponent } from './portfolio/projectfolio/projectfolio.component';
import { HobbiefolioComponent } from './portfolio/hobbiefolio/hobbiefolio.component';
import { DetailprojectComponent } from './portfolio/projectfolio/detailproject/detailproject.component';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { AdminProjectsComponent } from './vitrine/admin/admin-projects/admin-projects.component';
import { AdminProjectInfoComponent } from './vitrine/admin/admin-project-info/admin-project-info.component';

const appRoutes:Routes=[
{path:'',component:PortfolioComponent},
{path:'authentification',component:AuthComponent},
{path:'admin/user',canActivate:[AuthGardService],component:AdminUserComponent},
{path:'admin/works',canActivate:[AuthGardService],component:AdminWorksComponent},
{path:'admin/studies',canActivate:[AuthGardService],component:AdminStudiesComponent},
{path:'admin/hobbies',canActivate:[AuthGardService],component:AdminHobbiesComponent},
{path:'admin/projects',canActivate:[AuthGardService],component:AdminProjectsComponent},
{path:'admin/projects/:id',canActivate:[AuthGardService],component:AdminProjectInfoComponent},
{path:'portfolio',component:PortfolioComponent},
{path:'portfolio/studies',component:StudiesfolioComponent},
{path:'portfolio/hobbies',component:HobbiefolioComponent},
{path:'portfolio/experiences',component:WorkfolioComponent},
{path:'portfolio/projects',component:ProjectfolioComponent},
{path:'portfolio/projects/:id',component:DetailprojectComponent},
{path:"**",redirectTo:'portfolio'}
];

@NgModule({
  declarations: [
    AppComponent,
    VitrineComponent,
    SidebarComponent,
    NavbarHeaderComponent,
    MainComponent,
    ProjectComponent,
    ProjectMainComponent,
    LearningComponent,
    LearningMainComponent,
    HobbiesComponent,
    HobbiesMainComponent,
    WorkComponent,
    WorkMainComponent,
    AuthComponent,
    AdminUserComponent,
    AdminWorksComponent,
    AdminStudiesComponent,
    AdminHobbiesComponent,
    PortfolioComponent,
    HeaderComponent,
    StudiesfolioComponent,
    FooterComponent,
    WorkfolioComponent,
    ProjectfolioComponent,
    HobbiefolioComponent,
    DetailprojectComponent,
    AdminProjectsComponent,
    AdminProjectInfoComponent,

  ],
  imports: [
  [NgbModule],
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [ 

  NavigateService,
  UserService,
  AuthService,
  AuthGardService

  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
