import { Component, OnInit } from '@angular/core';
import { ProjectsService } from '../../../portfolio/service/projects.service';
import { Subscription } from 'rxjs/Subscription';
import { Router } from '@angular/router';

@Component({
  selector: 'app-project-main',
  templateUrl: './project-main.component.html',
  styleUrls: ['./project-main.component.scss']
})
export class ProjectMainComponent implements OnInit {

    constructor(private projectsService : ProjectsService,
    	private router : Router) { }

projects:string[];
projectSubscription:Subscription;

  ngOnInit(): void {
  	this.subscribeToProjects();
  }


  subscribeToProjects(){
    this.projectSubscription = this.projectsService.projectSubject.subscribe((projects)=>{
      this.projects = projects;
    });
    this.projectsService.emitProjects();
  }

   onViewProject(id: number) {
    this.router.navigate(['/portfolio', 'projects', id]);
  }

  ngOnDestroy(){
    this.projectSubscription.unsubscribe();
  }

}
