import { Injectable } from '@angular/core';
import * as firebase from 'firebase';
import { User } from '../models/user.model';
import { Subject } from 'rxjs/Subject';

@Injectable({
  providedIn: 'root'
})
export class UserService {

	user: User;
	userSubject = new Subject<User>();
	uploadRep:[];
  constructor() {
  	firebase.database().ref('/public/user').on('value',(data)=>{
  		this.user = new User(data.val().firstName, 
  							data.val().lastName, 
  							data.val().birthday);
  		this.user.logoPath = data.val().logoPath ? data.val().logoPath : '';
  		this.user.logoDownloadPath='';
  		if(data.val().logoPath) this.getLogoDownloadUrl();
  		this.emitUser();
  	});
  	//console.log(this.user);
  	
   }

   emitUser(){
   	this.userSubject.next(this.user);
   }

   getLogoDownloadUrl(){
   		firebase.storage().ref().child(this.user.logoPath).getDownloadURL().then((url:string)=>{
   			this.user.logoDownloadPath = url;
   			this.emitUser();
  			},(error)=>{
  				console.log(error);
  			});
   }


   uploadFile(file : File){
  	return new Promise((resolve,reject)=>{
  		const almostUniqueFileName = Date.now().toString();
  		const upload = firebase.storage().ref()
  		.child('/public/user/'+ almostUniqueFileName + file.name)
  		.put(file);

  		upload.on(firebase.storage.TaskEvent.STATE_CHANGED, ()=>{
  			console.log("chargment ... ");
  		},(error) =>{
  			console.log("erreur : " + error);
  			reject()
  		},()=>{

  			upload.snapshot.ref.getDownloadURL().then(function(downloadURL) {
        		console.log('File available at', downloadURL);
        		//console.log(this.uploadRep)
        		const uploadRep = [downloadURL,"/public/user/"+almostUniqueFileName+file.name];
        		
        		console.log(uploadRep);
        		resolve(uploadRep);
      		});
  		});
  	});
  }

  savephotoUser(photoPath:string){
  	this.user.logoPath = photoPath;
  	console.log(this.user)
  	firebase.database().ref("/public/user").set(this.user);
  }

  saveUser(user :User){

    if(user.logoPath != this.user.logoPath){
      firebase.storage().ref().child(this.user.logoPath).delete().then(()=>{
        console.log( this.user.logoPath +' deleted')
      },(error)=>{
        console.log(error);
      });
    }
    firebase.database().ref("/public/user").set(user);
  }

  removeOneFile(path){
    firebase.storage().ref().child(path).delete().then(()=>{
        console.log( path +' deleted')
      },(error)=>{
        console.log(error);
      });
  }



}
